.standings_table{
    width: 100%;
    row-gap: 10px;
}
.standings_table th{background-color: blue; color: white;}
.standings_table tr:nth-child(even){background-color: #f2f2f2;}

.standings_table tr:hover {background-color: #ddd;}
.tLogo40x{
    /* background-image: url('https://www.iplt20.com/resources/v4.20.1/i/sprites/tLogo100x-sprite.png'); */
    background-image: url('https://www.iplt20.com/resources/v4.20.1/i/sprites/tLogo40x-sprite.png');
    background-repeat: no-repeat;
    width:40px;
    height:40px;
}
.tLogo40x.CSK{background-position:-40px 0}.tLogo40x.DC{background-position:-120px -40px}.tLogo40x.DD{background-position:0 -40px}.tLogo40x.DEC{background-position:-40px -40px}.tLogo40x.GL{background-position:-80px 0}.tLogo40x.KKR{background-position:-80px -40px}.tLogo40x.KTK{background-position:0 -80px}.tLogo40x.KXIP{background-position:-40px -80px}.tLogo40x.MI{background-position:-80px -80px}.tLogo40x.PBKS{background-position:-120px 0}.tLogo40x.PWI{background-position:0 0}.tLogo40x.RCB{background-position:-120px -80px}.tLogo40x.RPS{background-position:0 -120px}.tLogo40x.RR{background-position:-40px -120px}.tLogo40x.SNO{background-position:-80px -120px}.tLogo40x.SPN{background-position:-120px -120px}.tLogo40x.SRH{background-position:-160px 0}.tLogo40x.TBD{background-position:-160px -40px}.tLogo40x.TBL{background-position:-160px -80px}.tLogo40x.TRL{background-position:-160px -120px}.tLogo40x.VEL{background-position:0 -160px}
@media (-webkit-min-device-pixel-ratio:2),(min-resolution:192dpi){.tLogo40x{background-image:url('https://www.iplt20.com/resources/v4.20.1/i/sprites/tLogo40x-sprite@x2.png');background-size:200px 200px}}